import React from 'react'
const Word = ({ selectedCharacter, correctLetters}) => {
    return (
        <div className="word">
            {selectedCharacter.split('').map((letter, i) => {
                return (
                    <span className="letter" key={i}>
                        {correctLetters.includes(letter) ? letter : ''}
                    </span>
                )
            })}
        </div>
    )
}

export default Word